<template>
     <div>
        <v-row justify="center" align="center">
            <v-col cols="12" md="10" sm="12">
                <v-card>
                    <v-form v-model="valid">
                        <v-card-title class="justify-center">
                            <v-icon>lock</v-icon>
                            <span class="subtitle-1 font-weight-bold ml-2">Change your Password</span>
                        </v-card-title>
                        <v-card-text>
                            <v-text-field
                            v-model="password"
                            outlined
                            :rules="[required('Password')]"
                            prepend-inner-icon="lock"
                            label="New Password"
                            type="password"
                            ></v-text-field>
                            <v-text-field
                            v-model="confirmPassword"
                            :rules="[required('Confirm Password'), passwordConfirmationRule]"
                            label="Confirm Password"
                            outlined
                            prepend-inner-icon="lock"
                            type="password"
                            :disabled="!password"
                            ></v-text-field>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn
                            block
                            x-large
                            color="primary"
                            @click.stop="changePassword"
                            :disabled="!valid"
                            >Update Password
                            </v-btn>
                        </v-card-actions>
                    </v-form>
                </v-card>        
            </v-col>
        </v-row>
     </div>           
</template>
<script>
import {mapActions,mapGetters} from "vuex"
export default {
    name:"ChangePassword",
    data:()=>{
        return{
            valid: false,
            password:'',
            confirmPassword:'',
            required(fieldName) {
                return v => (v && v.length > 0) || `${fieldName} Required`;
            },
        }
    },

    computed:{
        ...mapGetters(["retrieveUser"]),
        passwordConfirmationRule() {
            return () =>this.password === this.confirmPassword ||"Password must match";
        },
    },
    methods:{
        ...mapActions(["updatePassword","setSnackbar"]),
        changePassword(){
            this.error=false
            this.errorMessage=''
            const rec = Object.assign(
                {},
                {userId: this.retrieveUser.userNum},
                {sessionKey: this.retrieveUser.sessionKey},
                {password: this.password}
            );
            this.updatePassword(rec).then((response)=>{
                if(response.success == true){
                    this.password=''
                    this.confirmPassword=''
                    this.setSnackbar({text:'Password Updated'})
                    this.$router.push({name:'UserAccount'})
                }else{
                    this.setSnackbar({text:response.message, color:'error'})
                }
            }).catch((er)=>{
                console.log(er)
            })
        }
    },
    

}
</script>